import calendarInit from "./calendar.js";
export default {
  name: "calendar",
  props: ["multiple"],
  data: function data() {
    return {
      openClass: "modal-out",
      bgOpen: "bg-out",
      toastClass: "toast-out",
      toastText: "",
      calendarVal: "请选择日期",
      toolbarYM: "",
      weekHeader: "",
      monthsData: {}
    };
  },
  created: function created() {
    this.calendarInitParams = new calendarInit.Calendar({
      multiple: this.multiple,
      monthsVM: this,
      btnShow: false
    });
  },
  methods: {
    calendarOpen: function calendarOpen() {
      this.calendarInitParams.open(this);
    },
    calendarOnChange: function calendarOnChange(p, values, displayValues) {
      var calendarVal = "";
      displayValues.map(function (val, num) {
        calendarVal = calendarVal + val;
      });
      this.calendarVal = calendarVal == "" ? "请选择日期" : calendarVal;
      this.$emit("change", p, values, displayValues);
    }
  }
};